import React, { Component } from 'react';
import './index.css';

// import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import Buscador from './components/Buscador.js';
import Resultado from './components/resultado';
class App extends Component {


  datosBusqueda = (termino) => {
    this.setState({
      termino: termino,
      pagina: 1
    }, () => {
      this.consultarAPI();
    });
  }

  consultarAPI = () => {
    const pagina = this.state.pagina;
    const url = `https://pixabay.com/api/?key=15585113-49021e27a434c40bf322ae3e8&&q=${this.state.termino}&per_page=30&page=${pagina}`;
    console.log(url);

    fetch(url)
      .then(respuesta => respuesta.json())
      .then(resultado => this.setState({ imagenes: resultado.hits }))
  }

  state = {
    termino: "",
    imagenes: [],
    pagina: ''
  }


  paginaAnterior = () => {
    //Leer el state de la pagina actual
    let pagina = this.state.pagina;
    if (pagina === 1) {
      return null;
    }
    //Sumar el state de la pagina actual
    pagina--;
    //Agregar el cambio a el state de la pagina actual
    this.setState({ pagina }, () => { this.consultarAPI();this.scroll(); });
  }
  paginaSiguiente = () => {
    //Leer el state de la pagina actual
    let pagina = this.state.pagina;
    //Sumar el state de la pagina actual
    pagina++;
    //Agregar el cambio a el state de la pagina actual
    this.setState({ pagina }, () => { this.consultarAPI();this.scroll(); });

  }

  scroll=()=>{
    const elemento=document.querySelector('.jumbotron');
    elemento.scrollIntoView('smooth','end');
  }

  render() {
    // return (
    // <Router>

    //   <Link to="/">Home</Link>
    //   <Link to="/post">Post</Link>


    // <Route exact path="/" render={() => {
    return <div>
      <div className="container">
        <div className="jumbotron">
          <p className="lead text-center">Buscador de imagenes </p>
          <Buscador
            datosBusqueda={this.datosBusqueda}
          />
        </div>



      </div>
      <Resultado paginaSiguiente={this.paginaSiguiente} paginaAnterior={this.paginaAnterior} Imagenes={this.state.imagenes}></Resultado>

    </div>


  }
  // }}>
  // </Route>
  // <Router path="/post" component={Post} />


  // </Router >

  // )
}



export default App;
