import react, { Documents } from "react";

const Imagen = (props) => {

    const{largeImageURL,likes,previewURL,tags,views}=props.imagen;

    return (
        <div className="col-12 col-sm-12 col-md-6 col-lg-4 col-xl-3 mb-4">
            <div className="card">
                <img src={largeImageURL} width="" alt="Ocurrio un error" className="card-img-top row-imagen"/>
                <div className="card-body">
                    <p className="card.text"><i class="uil uil-heart"></i>{likes}   <i class="uil uil-eye"> </i>{views}</p>
                    <a href={largeImageURL} target="_blank" className="btn btn-primary btn-block"> Ver</a>

                </div>
            </div>
        </div>
);

}

export default Imagen;