import react from "react";

const Paginacion=(props)=>{
    return(
        <div className="d-flex justify-content-center">
            <button onClick={props.paginaAnterior} typy="button" className="btn btn-info mr-1">&larr; Anterior </button>
            <button onClick={props.paginaSiguiente}typy="button" className="btn btn-info">Siguiente &rarr;</button>
        </div>
    )
}

export default Paginacion;